import React, { useRef, useState, useEffect } from 'react'
// import Sdata from './Sdata'
import store from '../store/store';
import { useParams, useHistory } from 'react-router-dom';
import axios from 'axios';


export default function Form() {

    let file = useRef();
    let price = useRef();
    let dis = useRef();

    let history = useHistory();
    let params = useParams();

    let [ad, setAd] = useState({});

    useEffect(() => {

        axios.get('/load_ad?id=' + params.id).then((resp) => {

            setAd(resp.data);

        });

    }, []);

    async function addData(evt) {
        evt.preventDefault();

        let simpleFormData = {
            price: price.current.value,
            title: dis.current.value,
            id: params.id
        };

        if (file.current.files.length) {

            simpleFormData = new FormData();
            simpleFormData.append('id', params.id);
            simpleFormData.append('price', price.current.value);
            simpleFormData.append('title', dis.current.value);
            simpleFormData.append('meriFile', file.current  .files[0]);

        }

        let resp = await axios.put('/update_ad', simpleFormData);

        history.push('/');

        store.dispatch({
            type: "Update_DATA",
            payload: resp.data
        })
    }

    return (
        <div className="container contact_div">
            <div className="row" >
                <div className="col-md-4 col-8 mx-auto">


                    <form onSubmit={addData}>
                        <div class="mb-3">
                            <label for="exampleInputEmail1" class="form-label">Title</label>
                            <input defaultValue={ad.title} type="text" ref={dis} class="form-control" id="exampleInputEmail1" aria-describedby="emailHelp" />
                        </div>
                        <div class="mb-3">
                            <label for="exampleInputPassword1" class="form-label">price</label>
                            <input defaultValue={ad.price} type="number" ref={price} class="form-control" id="exampleInputPassword1" />
                        </div>
                        <div class="mb-3 form-check">
                            <input onChange={(evt) => {

                                setAd({ ...ad, imgsrc: URL.createObjectURL(evt.target.files[0]) })

                            }} type="file" ref={file} class="form-check-input" id="exampleCheck1" />
                            <label class="form-check-label" for="exampleCheck1">Upload Picture</label>
                        </div>
                        <img src={ad.imgsrc} />
                        <br />
                        <button type="submit" class="btn btn-primary">Submit</button>
                    </form>
                </div>
            </div>
        </div>
    )
}

