// import Sdata from '../Sdata'
import { combineReducers, createStore } from 'redux';

let intitialDAta = [];

let initialUserData = {user:null};

function userReducer(state = initialUserData, action) {

    state = { ...state };

    switch (action.type) {

        case 'USER_LOGIN':
            state.user = action.payload;
            break;

        case 'USER_LOGOUT':
            state.user = null;
            break;


    }

    return state;
}

function dataReducer(state = intitialDAta, action) {
    state = [...state];


    switch (action.type) {

        case 'ADD_DELETED':

            let milgyaAd = state.find((ad) => {

                if (ad.id == action.id) {
                    return true;
                }

            })

            let myIndex = state.indexOf(milgyaAd);
            state.splice(myIndex, 1);

            break;


    }



    if (action.type == "Update_DATA") {
        let miligyAd = state.find((ad) => {

            if (ad.id == action.payload.id) {
                return true;
            }

        })
        let adINdex = state.indexOf(miligyAd);
        state[adINdex] = action.payload;
    }


    if (action.type == "LOADED_ADS") {
        state = [...action.ads]
    }

    if (action.type == "ADD_DATA") {
        state.push(
            {
                imgsrc: action.payload.imgsrc,
                price: action.payload.price,
                title: action.payload.title
            })
    }
    return state
}

let allReducer = combineReducers({ dataReducer, userReducer });
let store = createStore(allReducer);
export default store;