import React, { useRef, useState } from 'react'
import Sdata from './Sdata'
import store from './store/store';

import axios from 'axios';

export default function Form() {

    let file=useRef();
    let price=useRef();
    let dis=useRef();

    async function addData(evt){
       evt.preventDefault();
       
// simple JSON data
// {} send krden

// file wagera
// form data use krna parega

// let fm = new FormData();
// fm.append('price', price.current.value);
// fm.append('title', dis.current.value);
// fm.append('ad_thumb', file.current.files[0]);


     let resp =  await axios.post('/create_ad', {
       price: price.current.value,
       title: dis.current.value,
     });
   
       store.dispatch({
         type:"ADD_DATA",
         payload:resp.data
       })
    }

    return (
      <div className="container contact_div">
        <div className="row" >
        <div className="col-md-4 col-8 mx-auto">

       
     <form onSubmit={addData}>
  <div class="mb-3">
    <label for="exampleInputEmail1" class="form-label">Title</label>
    <input type="text" ref={dis} class="form-control" id="exampleInputEmail1" aria-describedby="emailHelp"/>
  </div>
  <div class="mb-3">
    <label for="exampleInputPassword1" class="form-label">price</label>
    <input type="number" ref={price} class="form-control" id="exampleInputPassword1"/>
  </div>
  <div class="mb-3 form-check">
    <input type="file" ref={file} class="form-check-input" id="exampleCheck1"/>
    <label class="form-check-label" for="exampleCheck1">Upload Picture</label>
  </div>
  <br/>
  <button type="submit" class="btn btn-primary">Submit</button>
</form>
         </div>
    </div>
    </div>
    )
}

