import React,{useState} from 'react'
import Card from './card/Card'
import { connect } from 'react-redux';
import axios from 'axios';
import store from '../components/store/store';


 function Cards(props) {

    // let [data, setData]=useState(props.dataReducer);

    async function deleteItem(id){
      
      // '/delete_ad?ad_id=10'

      await axios.delete('/delete_ad?ad_id='+id);

      store.dispatch({
        type:"ADD_DELETED",
        id:id
      });

      // data.splice(id, 1)
      // setData([...data]);
      alert("delete items")
    }
    return (
     

        <div>
            

            {
            
      props.dataReducer
      .filter((ad)=>{
        return props.search ? ad.title.includes(props.search) : true
      })
      .map((item, index)=>{


        return  <Card data={item} deleteItem={deleteItem} index={index}/>
      })
    }
        </div>
    )
}
export default connect(function(store){

  return store
})(Cards)