import Nav from './components/Nav'
import Card from './components/card/Card'
import Form from './components/Form';
import './App.css'
import Sdata from './components/Sdata'
import { useState, useEffect } from 'react'
import { Switch, Route, Redirect, useHistory } from "react-router-dom"
import Cards from './components/Cards';
import EditAd from './components/editCard/editCard';

import { useForm } from "react-hook-form";


import axios from 'axios';
import store from './components/store/store';

function Signup() {

  const { register, handleSubmit, watch, formState: { errors } } = useForm();

  const onSubmission = async (formData) => {

    await axios.post('/signup', formData);

  }

  return <form onSubmit={handleSubmit(onSubmission)}>
    <div>
      <input {...register("name")} type="text" />
    </div>
    <div>
      <input  {...register("email")} type="email" />
    </div>
    <div>
      <input  {...register("password")} type="password" />
    </div>
    <button>Submit User</button>
  </form>

}


function Login() {

  const { register, handleSubmit, watch, formState: { errors } } = useForm();


  let history = useHistory();

  const onSubmission = async (formData) => {

    try {
      let resp = await axios.post('/login', formData);

      if (resp.data.user.id) {


        localStorage.setItem('token', resp.data.token)


        store.dispatch({
          type: "USER_LOGIN",
          payload: resp.data.user
        })

        history.push('/dashboard');

      } else {
        alert("user nahi j");
      }

    } catch (e) {

      console.log(e);
    }

  }

  return <form onSubmit={handleSubmit(onSubmission)}>
    <div>
      <input  {...register("email")} type="email" />
    </div>
    <div>
      <input  {...register("password")} type="password" />
    </div>
    <button>Login</button>
  </form>

}



export default function App() {

  useEffect(() => {

    async function fetchData() {

      try {
        let respUser = await axios.get('/check_session?token=' + localStorage.getItem('token'));

        if (respUser.data.id) {
          store.dispatch({
            type: "USER_LOGIN",
            payload: respUser.data
          })

        }
      } catch (e) {
        alert('token garbaring');
      }

      let resp = await axios.get('/get_ads');

      store.dispatch({
        type: "LOADED_ADS",
        ads: resp.data
      });


    }

    fetchData();


  });


  let [search, setSearch] = useState()
  return (
    <div >
      <Nav setSearch={setSearch} />
      <Switch>
        <Route path="/form" component={Form} />

        <Route path="/signup" component={Signup} />
        <Route path="/login" component={Login} />

        <Route path="/edit/:id" component={EditAd} />

        <Route path="/" render={() => {
          return <Cards search={search} />
        }} />

      </Switch>




    </div>
  );
}